import { Button } from "@mui/material";
import { RandomRoom } from "../state/RandomRoom";
//""
export const nextButtonView = (
  setPage,
  setState,
  nowState,
  setHandCard,
  deck,
  setEnemy,
  setEnemyAction,
  setBattleDeck,
  setchoiceOpen,
  setEvent,
  setImg,
  setshopButtonOpen,
  setHealButtonOpen,
  setDeckDeleteButtonOpen
) => {
  return (
    <div
      style={{
        position: "absolute",
        top: "500px",
        left: "850px",
        minWidth: "500px",
      }}
    >
      <Button
        fullWidth
        style={{ backgroundColor: "rgba(0,0,0,0.6)", margin: 20 }}
        onClick={() => {
          setHealButtonOpen(true);
          setDeckDeleteButtonOpen(true);
          setshopButtonOpen(true);
          setImg("");
          setchoiceOpen(false);
          RandomRoom(
            setPage,
            setHandCard,
            deck,
            setEnemy,
            setEnemyAction,
            setBattleDeck,
            setEvent
          );
          setState({
            health: nowState.health,
            maxHealth: nowState.maxHealth,
            money: nowState.money,
            roomNo: nowState.roomNo + 1,
            energy: nowState.energy,
            guard: 0,
            power: 0,
            sanity: 0,
          });
        }}
      >
        <p style={{ fontSize: 18, color: "white" }}></p>
      </Button>
    </div>
  );
};
//
export const moneyView = (money) => {
  return (
    <div
      style={{
        position: "absolute",
        top: "0%",
        left: "0%",
        marginTop: "95px",
        marginLeft: "480px",
      }}
    >
      <p style={{ fontSize: "30px", userSelect: "none" }}>${money}</p>
    </div>
  );
};
